.utility{
    -ms-flex-line-pack: center;
        align-content: center;
}

.DannyBio{
    font-size: 1.2rem;
    width: 65vw;
    padding: 1rem;
    margin: 10vh auto 0;
    line-height: 120%;
    background-color: rgba(155,155,155, .35);
    border: 1px solid #b298dc;
    border-radius: 3px;
    -webkit-transition: 2s ease-in-out;
    -o-transition: 2s ease-in-out;
    transition: 2s ease-in-out;
}

.DannyBio:hover{
    -webkit-transform: scale(1.1) translateY(2vh);
        -ms-transform: scale(1.1) translateY(2vh);
            transform: scale(1.1) translateY(2vh);
    border: 2px solid #b298dc;
}

.h1{
    font-family: "Indie Flower";
    text-align: center;
    color: #9F2DBD;
    text-decoration: underline;
}

.wikiLink {
    font-size: 1.3rem;
    margin: 5rem;
    text-align: center;
}

.wikiLink a{
    text-decoration: none;
}

.wikiLink a:hover{
    text-decoration: underline;
}




