body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  font-size: 62.5%;

}

body:before {
	content:"";
	position:fixed;
	left:0;
	top:0;
	right:0;
	bottom:0;
	z-index:-1; 
  
  background-image: -webkit-gradient(
      linear,
       left top, right bottom,
       from(rgba(185,250,248, 0.6)),
      to(rgba(184,208,235, 0.6))), url("./images/DannyPeace.jpg"); 
  
  background-image: -webkit-linear-gradient(
      top left,
       rgba(185,250,248, 0.6),
      rgba(184,208,235, 0.6)), url("./images/DannyPeace.jpg"); 
  
  background-image: -o-linear-gradient(
      top left,
       rgba(185,250,248, 0.6),
      rgba(184,208,235, 0.6)), url("./images/DannyPeace.jpg"); 
  
  background-image: linear-gradient(
      to bottom right,
       rgba(185,250,248, 0.6),
      rgba(184,208,235, 0.6)), url("./images/DannyPeace.jpg");
  height: 100vh;
  background-size:cover;
  background-repeat: repeat-x;
}


